html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --verydblue: hsl(230, 17%, 14%);
  --desatdbblue: hsl(228, 28%, 20%);
  --desatblue: hsl(228, 34%, 66%);
  --white: hsl(0, 0%, 100%);
  --paleblue: hsl(225, 100%, 98%);
  --lgrayishblue: hsl(227, 47%, 96%);
  --dgrayishblue: hsl(228, 12%, 44%);
  --verydblue2: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.card {
  padding: 2.0625rem;
}

.toggle_button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--white);
}